import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { Course } from '../../classes/course';
import { CourseService } from '../../angular-services/course.service';
import { CourseCategory } from '../../classes/course-category';

@Component({
  selector: 'app-pagina-curso',
  templateUrl: './pagina-curso.component.html',
  styleUrls: ['./pagina-curso.component.css']
})
export class PaginaCursoComponent implements OnInit {

  course: Course = new Course();
  courseCategory: CourseCategory = new CourseCategory();
  data: string = '';
  path: string = '';
  loading: boolean = true;

  constructor(private courseService: CourseService, private location: Location, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getCourse();
  }

  getCourse() {
    this.loading = true;
    const id = this.route.snapshot.paramMap.get('id');
    this.courseService.getCourse(id).subscribe(
      response => {
        this.loading = false;
        this.course = response.course;
        this.courseCategory = response.category;
      }
    );
  }

  goBack() {
    this.location.back();
  }

}

